const sr = ScrollReveal({
    distance: "60px",
    duration: 1000,
    delay: 0,
    easing: "ease-out",
    opacity: 0,
    mobile: false,
    viewFactor: .75
});

// Nav-Items
sr.reveal(".nav-item", {
    distance: "100%",
    duration: 750,
    delay: 250,
    easing: "ease-in-out",
    opacity: 0,
    mobile: false,
    viewFactor: 0,
    origin: "top",
    interval: 100
})
// Website Logo
sr.reveal(".navbar-brand", {
    distance: "100%",
    duration: 1250,
    delay: 350,
    easing: "ease-in-out",
    opacity: 0,
    mobile: false,
    viewFactor: 0,
    origin: "left",
    interval: 100
})

// Slide origin
sr.reveal('.slide-from-left', { origin:"left" });
sr.reveal('.slide-from-right', { origin:"right" });
sr.reveal('.slide-from-top', { origin:"top" });
sr.reveal('.slide-from-bottom', { origin:"bottom" });

// Slide delay
sr.reveal('.slide-delay-100', { delay: 100 });
sr.reveal('.slide-delay-200', { delay: 200 });
sr.reveal('.slide-delay-300', { delay: 300 });
sr.reveal('.slide-delay-400', { delay: 400 });
sr.reveal('.slide-delay-500', { delay: 500 });
sr.reveal('.slide-delay-600', { delay: 600 });
sr.reveal('.slide-delay-700', { delay: 700 });
sr.reveal('.slide-delay-800', { delay: 800 });
sr.reveal('.slide-delay-900', { delay: 900 });
sr.reveal('.slide-delay-1000', { delay: 1000 });

// viewFactor
sr.reveal('.slide-delay-0', { viewFactor: 0 });
sr.reveal('.slide-delay-10', { viewFactor: .1 });
sr.reveal('.slide-delay-20', { viewFactor: .2 });
sr.reveal('.slide-delay-30', { viewFactor: .3 });
sr.reveal('.slide-delay-40', { viewFactor: .4 });
sr.reveal('.slide-delay-50', { viewFactor: .5 });
sr.reveal('.slide-delay-60', { viewFactor: .6 });
sr.reveal('.slide-delay-70', { viewFactor: .7 });
sr.reveal('.slide-delay-80', { viewFactor: .8 });
sr.reveal('.slide-delay-90', { viewFactor: .9 });
sr.reveal('.slide-delay-100', { viewFactor: 1 });